@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Element/AmountFormatter";
@use "@app/bancor-components/Element/DateTime";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/Border";

$card-width: 328px;
$download-item-height: 44px;

@mixin footerButtons {
    @include Flex.set($dir: row, $align: center, $justify: flex-end);
    @include Margin.set($top: beta, $bottom: beta);
    width: Layout.get-width(kappa);

    #{Button.$selector} {
        min-width: Layout.get-space(iota);
    }

    @include screen.lg {
        @include Margin.set($top: none, $bottom: none);
    }
}

.internalContainer {
    @include Margin.set($left: beta, $right: beta, $top: gamma);
    @include Flex.set($dir: column, $grow: alpha);
    gap: Layout.get-space(delta);

    @include screen.md {
        @include Margin.set($left: delta, $right: delta, $top: delta);
    }

    @include screen.xl {
        grid-column: 2;
    }
    #{Text.$selector}.title {
        @include Margin.set($bottom: alpha);
        @include Font.set($size: delta);
        @include Color.set($fg: "black");

        @include screen.md {
            @include Font.set($size: kappa);
            @include Margin.set($bottom: beta);
        }
    }

    #{Text.$selector}.subtitle {
        @include Font.set($family: default, $weight: default);
    }

    .cardsLoan {
        @include Display.set($display: grid);
        grid-template-columns: repeat(auto-fill, minmax($card-width, 1fr));
        gap: Layout.get-space(beta);
    }

    .table {
        table-layout: auto;

        & tbody tr td:last-child {
            overflow: visible;
            width: 10%;
        }

        & tbody tr td time {
            @include Color.set($fg: black);
        }
    }

    .tableContainer {
        overflow: visible;
    }

    .tenureHeader {
        @include Flex.set($justify: between, $align: center);

        & div {
            @include Flex.set($justify: between, $align: center);
            gap: Layout.get-space(beta);

            & #{Button.$selector} {
                @include Padding.set($preset: none);
                @include Margin.set($left: alpha);
                @include Flex.set($align: center);
                @include Color.set($bg: "transparent");
                @include Border.set($style: default);
                cursor: pointer;
            }
        }
    }
}

.loanDetail {
    @include Margin.set($left: beta, $right: beta, $top: gamma);
    @include Flex.set($dir: column, $grow: alpha);
    gap: Layout.get-space(delta);

    @include screen.md {
        @include Margin.set($left: delta, $right: delta, $top: delta);
    }

    @include screen.xl {
        grid-column: 2;
    }

    header {
        @include Flex.set($justify: between, $align: center);
    }

    .cardsDetail {
        @include screen.sm {
            display: contents;
        }
        gap: Layout.get-space(beta);
        .amount {
            #{Text.$selector},
            #{AmountFormatter.$selector} > data,
            #{AmountFormatter.$selector} > span {
                @include Color.set($fg: "blue-900");
            }
        }
        @include screen.xl {
            @include Display.set($display: grid);
            grid-template-columns: repeat(auto-fit, minmax(Layout.get-space(kappa), 1fr));

            word-break: break-all;
        }
    }

    .sectionAccordion {
        button {
            width: auto;
        }

        & > div > div {
            @include Shadow.set($box: none);
        }

        .contentAccordion {
            @include Display.set($display: grid);
            @include Padding.set($top: beta);
            grid-template-columns: 1fr 1fr;
            gap: Layout.get-space(delta);
        }
    }

    .cardLoanAmount {
        @include Display.set($display: flex);
        @include Flex.set($justify: center, $align: center, $dir: column);

        #{AmountFormatter.$selector} {
            > span,
            > data {
                @include Font.set($size: kappa, $family: heading, $weight: bold);
                @include Color.set($fg: "black");
            }
        }

        #{Text.$selector}.title {
            @include Color.set($fg: "black");
        }
    }

    .tab {
        @include Margin.set($left: none, $right: none, $bottom: none);

        .contentInfo {
            @include Flex.set($justify: between, $align: center);
            @include Margin.set($bottom: beta);

            #{AmountFormatter.$selector} {
                > span,
                > data {
                    @include Font.set($size: gamma, $family: default);
                    @include Color.set($fg: "black");
                }
            }
            #{DateTime.$selector} {
                @include Font.set($size: gamma, $family: default);
                @include Color.set($fg: black);
            }
            #{Text.$selector}.value {
                @include Font.set($size: gamma, $family: default);
                @include Color.set($fg: black);
            }
        }

        .accordionSummary {
            @include Flex.set($justify: between, $align: center);
            @include Padding.set($right: alpha);

            .titleQuotas {
                @include Flex.set($align: center);

                #{Text.$selector} {
                    @include Padding.set($right: alpha);
                    min-width: Layout.get-width(fit);
                }
            }
        }

        .tagQuotas {
            @include Padding.set($right: alpha, $left: alpha);
            min-width: Layout.get-width(auto);
        }

        .accordionDetails {
            @include Padding.set($top: alpha);
            .contentQuotas {
                @include Flex.set($justify: between, $align: center);

                #{DateTime.$selector} {
                    @include Font.set($size: gamma, $family: default);
                    @include Color.set($fg: black);
                }
            }
        }
    }

    .tabsHeader {
        @include Margin.set($left: none);
    }

    & tbody tr td:last-child {
        overflow: visible;
    }

    .table {
        table-layout: auto;
        #{DateTime.$selector} {
            @include Color.set($fg: black);
        }

        .tag {
            min-width: auto;
        }
    }

    .tableContainer {
        overflow: visible;
    }

    .pictureName {
        height: $download-item-height;

        #{Button.$selector} {
            @include Flex.set($align: center);
            @include Color.set($bg: "transparent");
            @include Border.set($style: default);
            height: Layout.get-height(alpha);
            cursor: pointer;

            #{Text.$selector} {
                @include Margin.set($left: alpha);
                @include Color.set($fg: "gray-dark");
                @include Font.set($size: gamma, $transform: caps);
            }
        }
    }
    .modalInstallments {
        .modalBody {
            #{Text.$selector} {
                word-break: break-word;
                h1 {
                    @include Font.set($align: center, $size: iota);
                }
                a {
                    pointer-events: auto;
                }
            }
        }

        .modalFooter {
            @include footerButtons;
        }
    }
}
